








































export default {
	data() {
		return {
			//题目集合
			items: [],
			//得分
			total: 0,
			//当前题目
			thisQuestion: '',
			//当前题目的下标
			thisIndex: -1,
			//当前的题目答案
			thisAnswer: -1,
		}
	},
	onLoad() {
		this.init()
	},
	watch: {
		thisIndex: function(newQuestion, oldQuestion) {
			if(this.items.length != 0){
				this.thisAnswer = this.items[this.thisIndex].answer
			}
		}
	},
	computed: {
		pageIndex: function() {
			return this.thisIndex + 1
		},
	},
	methods: {
		init() {
			let that = this
			//初始化下标
			that.thisIndex = 0
			//初始化题目集合
			uni.request({
				url: "https://easy-mock.com/mock/5c796c30525b9135121eb173/weChat/getData",
				method: "GET",
				success: (res) => {
					that.items = res.data.data
					if (that.items) {
						//初始化题目
						that.thisQuestion = that.items[that.thisIndex]
					}
				},
				fail: (res) => {

				}
			})
		},
		goPrev() {
			let that = this
			let thisIndex = that.thisIndex
			if (thisIndex <= 0) {
				uni.showToast({
					title: "已经是第一题了!",
					icon: "none"
				})
				return
			}
			that.thisIndex--
			that.thisQuestion = that.items[thisIndex - 1]

		},
		goNext(ind) {
			let that = this
			let thisIndex = that.thisIndex

			if (thisIndex + 1 === that.items.length) {
				that.thisAnswer = ind
				that.items[thisIndex].answer = ind
				uni.setStorageSync('result', that.items)
				uni.navigateTo({
					url: '/pages/result/result'
				})
				return
			}

			if (isNaN(ind) && that.thisAnswer === -1) {
				uni.showToast({
					title: "你还没有选择题目呢",
					icon: "none"
				})
				return
			}

			//保存答案
			that.items[thisIndex].answer = ind

			//切换到下一题
			that.thisIndex++
			that.thisQuestion = that.items[thisIndex + 1]
		}
	}
}
